/* Center the image at the top */
.image-container-top {
  margin-top: -90px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  /* margin-bottom: 20px; */
}

@media (max-width: 600px) {
  .text-container {
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }

  .image-container-bottom {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%) rotate(180deg);
    margin-top: 20px;
  }
}

/* Styles for language switcher button */
.language-switcher-button {
  color: rgb(111, 60, 58);
  cursor: pointer;
  margin-right: 10px;
}

.rsvp-button {
  color: rgb(111, 60, 58);
  cursor: pointer;
  margin-right: 10px;
  height: 35px;
}

/* Center the name container */
.name-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.text-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

/* Styles for the home tab picture */
.home-tab-picture {
  display: block;
  width: 50%;
  /* Set the width to 50% */
  margin: 0;
  margin-bottom: 30px;
  box-sizing: border-box;
  border: 8px solid rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 1px 4px;
}

form {
  display: flex;
  flex-direction: column;
}

label {
  margin-bottom: 8px;
}

input,
select {
  margin-bottom: 16px;
  padding: 8px;
}

